    /**
         * @param filter (optional) 
         * @param patientId (optional) 
         * @param sorting (optional) 
         * @param skipCount (optional) 
         * @param maxResultCount (optional) 
         * @return Success
         */
    getSensitiveDiseasesForPatient(filter: string | null | undefined, patientId: number | null | undefined, sorting: string | null | undefined, skipCount: number | null | undefined, maxResultCount: number | null | undefined): Observable<Request<PagedResultDto<SensitiveDiseaseListDto>>> {
        let url_ = this.baseUrl + "/api/services/app/Patients/GetSensitiveDiseasesForPatient?";
        if (filter !== undefined)
            url_ += "Filter=" + encodeURIComponent("" + filter) + "&";
        if (patientId !== undefined)
            url_ += "PatientId=" + encodeURIComponent("" + patientId) + "&";
        if (sorting !== undefined)
            url_ += "Sorting=" + encodeURIComponent("" + sorting) + "&";
        if (skipCount !== undefined)
            url_ += "SkipCount=" + encodeURIComponent("" + skipCount) + "&";
        if (maxResultCount !== undefined)
            url_ += "MaxResultCount=" + encodeURIComponent("" + maxResultCount) + "&";
        url_ = url_.replace(/[?&]$/, "");

        return this.http.get<any>(url_);
    }
    
     
